}

/** Media Queries **/
.nav__grid {
  padding: 1.2rem 2rem;
  background-color: #484349;
  width: 100%;
  /* hacemos que la cabecera ocupe el ancho completo de la página */
  left: 0;
  /* Posicionamos la cabecera al lado izquierdo */
  top: 0;
  /* Posicionamos la cabecera pegada arriba */
  position: fixed;
  z-index: 9999;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__grid {
    padding: 1.2rem 10rem;
  }
}
.nav__logo {
  padding-top: 0.5rem;
  width: 13rem;
}
@media (min-width: 768px) {
  .nav__logo {
    width: 15rem;
    margin: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  position: fixed;
  left: 0;
  top: 6.7rem;
  width: 100%;
  align-items: center;
  padding: 5rem;
  height: calc(100% - 6rem);
  overflow-y: auto;
  left: 100%;
  transition: left 0.8s;
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  background-color: rgba(17, 25, 40, 0.75);
  border: 1px solid rgba(255, 255, 255, 0.125);
}
@media (min-width: 768px) {
  .navegacion {
    backdrop-filter: none;
    -webkit-backdrop-filter: none;
    background-color: none;
    border-radius: none;
    border: none;
    padding: 0;
    position: relative;
    width: auto;
    margin-right: 0;
    top: 0;
    background-color: transparent;
    align-items: center;
    flex-direction: row;
    margin-top: 0;
    left: 0;
  }
}
.navegacion__enlace {
  padding: 2rem 0;
  color: #FFFFFF;
  transition: 0.3s;
}
.navegacion__enlace:hover {
  color: #F28917;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    padding: 1rem 3rem;
    transition: 0.2s;
  }
  .navegacion__enlace:hover {
    color: #FFFFFF;
    border-bottom: 0.5rem solid #F28917;
    margin: 0.8rem;
    padding: 0.2rem 2rem;
    border-radius: 0.5rem;
  }
}

swiper-container {
  width: 100%;
  height: 75rem;
  background-color: #F28917;
  margin-top: 66.9px;
}
@media (min-width: 768px) {
  swiper-container {
    height: 69rem;
