.carousel {
  position: relative;
  width: 100%;
  height: 22rem; /* Ajusta la altura según tus necesidades */
  overflow: hidden;
}

.carousel-container {
  display: flex;
  width: fit-content;
  animation: carousel-slide 10s linear infinite; /* Ajusta la velocidad aquí (20s) */
}

.carousel-container img {
  width: 12rem; /* Ajusta el ancho de los logos según tus necesidades */
  height: auto;
  margin-right: 25px; /* Espacio entre los logos */
}

@keyframes carousel-slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%); /* Ajusta el porcentaje según el número de logos */
  }
}
